import { Component, OnInit } from '@angular/core';
import { MatTableDataSource, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-tagmappingview',
  templateUrl: './tagmappingview.component.html',
  styleUrls: ['./tagmappingview.component.css']
})
export class TagmappingviewComponent implements OnInit {
  displayedColumns: string[] = ['position', 'name'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);
  
  constructor(public dialogref:MatDialogRef<TagmappingviewComponent>) { }

  ngOnInit() {
  }
  savechanges(){
   
    this.dialogref.close();
      }
}
export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 12, name: 'sam', weight: 1.0079 },
  
 
];