import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { Tags } from '../tags/tags';
import { retry, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment.prod';


import { StringMap } from '@angular/compiler/src/compiler_facade_interface';
import { NewExternalSystems } from '../new-external-system/externalsystem';

@Injectable({
    providedIn: 'root'
})
export class TagsService {


    // private Url = 'http://localhost:9800/tags';
    private newUrl = environment.MEDIAN_URL;

    constructor(private http: HttpClient) { }

    handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                `Backend returned code ${error.status}, ` +
                `body was: ${error.error}`);
        }
        // return an observable with a user-facing error message
        return throwError(
            'Something bad happened; please try again later.');
    };

    getTagList(): Observable<Tags> {
        console.log("Reached service page");
        return this.http.get<Tags>(`${this.newUrl}` + "/tags/fetchalltags")
            .pipe(retry(2), catchError(this.handleError))
    }


    // saveNewExternalSystem(formData : FormData): Observable<any> {  
    //     console.log("Reached service.ts page");
    //     console.log("formData is :" + formData);
    //     return this.http.post(this.Url + '/save/',  formData);  
    // }

    extractXml(): Observable<Tags> {

        return this.http.get<Tags>(`${this.newUrl}` + "/tags/extractMessage")
            .pipe(retry(2), catchError(this.handleError))
    }

}