import { Component, OnInit } from '@angular/core';
import {MatDialog,MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import{TagmappingviewComponent} from './tagmappingview/tagmappingview.component';
import { Router } from '@angular/router';
@Component({
  selector: 'app-tagmapview',
  templateUrl: './tagmapview.component.html',
  styleUrls: ['./tagmapview.component.css']
})
export class TagmapviewComponent implements OnInit {
  constructor(private route:Router,
    private dialog:MatDialog) { }
  systemname="ITAX";
  Modulecode="IOS-91";
  servicecode="1200";
  processcode="1010000";
  communicationchannel="MDB"
  displayedColumns: string[] = ['position', 'name','targetname','required','default', 'weight'];
  dataSource = ELEMENT_DATA;
  ngOnInit() {
  }
  viewmap(){
    const dialogconfig=new MatDialogConfig();
    dialogconfig.disableClose=true;
    dialogconfig.autoFocus= true;
    dialogconfig.width="30%";
this.dialog.open(TagmappingviewComponent,dialogconfig);
  }
}
export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  targetname:string;
  required:string;
  default:string;
  
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, name: 'Hydrogen', weight: 1.0079,targetname:'ssss',required:'aaaaa',default:'mmmmm' },
  {position: 2, name: 'Helium', weight: 4.0026,targetname:'ssss',required:'aaaaa',default:'mmmmm' }
 
];