import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import {SelectionModel} from '@angular/cdk/collections';
import {MatDialog,MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { TaglistComponent } from '../taglist/taglist.component';
import { AddruleComponent } from './addrule/addrule.component';
@Component({
  selector: 'app-tagmapping',
  templateUrl: './tagmapping.component.html',
  styleUrls: ['./tagmapping.component.css']
})


export class TagmappingComponent implements OnInit {
  displayedColumns: string[] = ['position', 'name', 'weight', 'symbol','Validation'];
  selection = new SelectionModel<Element>(true, []);
  dataSource = ELEMENT_DATA;
  selected: string='default';
  constructor(private route:Router,
    private dialog:MatDialog) { }
  display:boolean=false;
  display2:boolean=true;
  percent=28;
  datatypevalue: string[] = ['NUMBER', 'DATE', 'Char', 'VarChar']
  systemname="ITAX";
  Modulecode="IOS-91";
  servicecode="1200";
  processcode="1010000";
  communicationchannel="MDB"
//   countries = [
//     new Country(1, 'USA' ),
//     new Country(2, 'India' ),
//     new Country(3, 'Australia' ),
//     new Country(4, 'Brazil')
//  ];
  ngOnInit() {
this.selected="default";
    
  }
  newtag(){
    this.display=true;
    this.display2=false;
    
    // this.route.navigateByUrl('/tagmapping/tagmappinglist');
  }
  map(){
    this.route.navigateByUrl('tagList/tagmapview');
  }
  addrule(){
    const dialogconfig=new MatDialogConfig();
    dialogconfig.disableClose=true;
    dialogconfig.autoFocus= true;
    dialogconfig.width="30%";
this.dialog.open(AddruleComponent,dialogconfig);
  }
}
export interface Element {
  name: string;
  position: number;
  weight: any;
  symbol: string;
  Validation:string
}
export class Country{
id:number;
country:string;
}
const ELEMENT_DATA: Element[] = [
  {position: 1, name: 'Hydrogen', weight: 1.0079, symbol: 'H',Validation:'asd'},
 { position: 1, name: 'Hydrogen', weight: 1.0079, symbol: 'H',Validation:'asd'}]
