import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { TagsService } from '../tags.service';
import {MatTableDataSource} from '@angular/material/table';


@Component({
  selector: 'app-taglist',
  templateUrl: './taglist.component.html',
  styleUrls: ['./taglist.component.css']
})
export class TaglistComponent implements OnInit {

  searchField;

  displayedColumns = ['tags', 'externalSystem', 'reqqueuenametopick', 'respqueuenametopick','errqueuenametopick','reqqueuenametosend','respqueuenametosend','Errqueuenametosend','creator','creatordtstamp','verifier','verifierdtstamp','latestAmendNo','verified','verifiedEver','recordStatus'];
  dataSource = new MatTableDataSource();
  constructor(private route:Router, private tagsService: TagsService) { }

  ngOnInit() {
    this.loadAllData();
  }

  newtag(){
    this.route.navigateByUrl('/tagList/newtag');
  } 

  loadAllData() {
    console.log("Reached ts");
    this.tagsService.getTagList().subscribe((response: any) => {
      this.dataSource.data = response;
      console.log(response);
    });
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

 clearSearchField() {
    this.searchField = '';
    this.dataSource.filter = '';
  }


}


